webpackJsonp([156488186261839],{

/***/ 543:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p>In the last segment of Acres of diamonds (Read [Acres Of Diamonds - Part 1: You Already Own the Acre Of Diamonds]({{ site.baseurl }}{% post_url 2017-08-31-acres-of-diamonds %}) if you have not already done so) we read the story of \"Acres of Diamonds\" and learned that each one of us, have enumerable opportunities awaiting us in our current places of employment. One of the worst things we can do is, to perceive that the grass is greener elsewhere, and jump from our current job to another in pursuit of a better job, more room for growth, and “a better team”. Instead, let us focus on what is available to us, and MAKE opportunities.</p>\n<p>A farmer once poked a pumpkin into the hole of a jug. The pumpkin began to grow and eventually could not grow anymore. When the farmer broke the glass, he had a pumpkin exactly the same size and shape, as was the jar. If we are not careful, each of us can do a similar thing of mistakenly poking ourselves into jugs. It is up to us to poke the jug. Not the job. Not the economy. We do it. </p>\n<p>People, who see their work as an opportunity for growth and development, are the people who succeed. The key to success is preparation. If we become so good what we do we will force the opportunity into the jobs that we have. </p>\n<p>It takes creative imagination to realize that diamonds do not look like diamonds in their rough state. </p>\n<blockquote>\n<p>\"Great opportunities lurk in every aspect of our work in which do\" -Earl Nightingale</p>\n</blockquote>\n<p>It is up to us to recognize and Prospect those diamonds. </p>\n<p>In order to begin prospecting the diamonds in our vocation, first begin by developing <strong>intelligent objectivity</strong>. Which really just means developing the ability to look at your job as a stranger would. A stranger who sees your job as an opportunity, a pasture that is much greener than his own.</p>\n<p>We can begin to do this by the following steps...</p>\n<p>Starting at the beginning. Within the framework of what industry or what profession does your job fall under.</p>\n<h2>Become a student of your industry</h2>\n<p>Ask yourself the following questions</p>\n<ul>\n<li>\n<p>Do you know everything you can know about your industry? How did it begin? Why did it begin who started it and when?</p>\n</li>\n<li>\n<p>What is your industries dollar volume? How fast has it grown in the last 20 years? What is the projected growth during the next 10 years?</p>\n</li>\n</ul>\n<p><strong>Become an expert in your field</strong>. You can do this in less than five years, and experts are the ones to write their own tickets in life.</p>\n<p>We should know as much about our job is a good doctor or a good lawyer knows about his or her own. This is what it means to be a professional.</p>\n<p>To become a professional we must learn and study the following subjects</p>\n<ol>\n<li>\n<p>Study your company and its industry</p>\n</li>\n<li>\n<p>Our job and our next step in our career</p>\n</li>\n<li>\n<p>Study people. Since getting along with people determines our range of our growth, we must learn how to deal with people.</p>\n</li>\n</ol>\n<p>Investing in becoming a professional <em>will</em> pay off in huge dividends. </p>\n<h2>We can further try to mine our acres of diamonds by thinking of ways we can increase our contribution to our company and our industry, and those whom we serve.</h2>\n<p>The minute you adopt this type of attitude you immediately become part of the top 5% of the world. You are affecting life rather than being affected by it. </p>\n<p>Also by changing your mental attitude takes care of two vitally important parts for successful living.</p>\n<ol>\n<li>\n<p>First you will find yourself becoming more interested and enthusiastic about your work and its future, and both interest in enthusiasm is contagious.</p>\n</li>\n<li>\n<p>You are building financial security, which will last a lifetime.</p>\n</li>\n</ol>\n<p>So keep this thought in your mind as often as possible on and off the job.</p>\n<blockquote>\n<p>\"Somewhere in your present work there lurks an opportunity which will bring you everything you could possibly want for yourself and your family. It will not be labeled opportunity. It will be hidden in common everyday garments. Like the dirty drinking cup which started the paper cup industry\"</p>\n</blockquote>\n<p>In closing, meditate and remember the following 12 points</p>\n<ol>\n<li>\n<p>Explore the work you are currently engaged in. You will find the riches, tangible or non-tangible you wish to have.</p>\n</li>\n<li>\n<p>Realize that your own pasture is green - and unlimited.</p>\n</li>\n<li>\n<p>The way you go about your job makes it bad or good. There is no such thing as a bad job.</p>\n</li>\n<li>\n<p>Let yourself grow - avoid self-limitation like the pumpkin in the jug.</p>\n</li>\n<li>\n<p>Start preparing yourself for opportunity - now. That way we are ready to take advantage of the opportunities in our future, the opportunities around us now.</p>\n</li>\n<li>\n<p>Put your imagination to work on ways of improving what you are now doing.</p>\n</li>\n<li>\n<p>Learn all that you can about your job, your company, and your industry.</p>\n</li>\n<li>\n<p>There is no limit to your growth potential. It follows similarly that since there is no limit to the growth of your industry, that there is no limit of your growth.</p>\n</li>\n<li>\n<p>Our economy needs and will reward the uncommon person who participates in its growth.</p>\n</li>\n<li>\n<p>Begin to build your library of reference material on your company, industry, your job, and people skills</p>\n</li>\n<li>\n<p>Set aside an hour a day for study and research.</p>\n</li>\n<li>\n<p>Remember the story of the Acres of Diamonds.</p>\n</li>\n</ol>","frontmatter":{"title":"Acres Of Diamonds - Part 2: How To Mine The Acres Of Diamonds"}}},"pathContext":{"slug":"/2017-09-25-acres-of-diamonds-p2/"}}

/***/ })

});


